.forget-page-container {
  display: flex;
  background: #1C1C1C;
  color: white;
  font-family: 'Poppins', sans-serif;
}

.forget-left-section,
.forget-right-section {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.forget-image-container {
  position: relative;
  border-radius: 0px;
  width: 630px;
  margin-left: -92px;
  margin-top: 42px;
  margin-bottom: 40px;
  height: 580px;
}

.forget-bg-img {
  width: 100%;
  height: 100%;
}

.forget-top-left-card {
  position: absolute;
  top: 42px;
  left: 40px;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.forget-active-learners {
  background: linear-gradient(to bottom right, #ffffff 0%, #6C6C6C 100%);
  padding: 4px 14px;
  border-radius: 8px;
  font-family: 'Poppins', sans-serif;
  font-size: 12px;
  margin-right: 16px;
  color: #000;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.forget-active-learners strong {
  font-weight: 600;
  font-size: 16px;
  font-family: Playfair Display;
  display: block;
}

.forget-passion-card {
  background: #30302E;
  padding: 36px 8px 6px;
  border-radius: 6px;
  font-family: 'Poppins';
  font-size: 12px;
  margin-bottom: -80px;
  margin-left: 18px;
  color: white;
}

.forget-passion-card strong {
  font-weight: 600;
}









.forget-top-right-card {
  position: absolute;
  top: 8px;
  right: 30px;
  display: flex;
  align-items: center;
  border-radius: 20px;
  font-size: 14px;
}

.forget-avatars {
  display: flex;
  align-items: center;
  margin-top: 33px;
  justify-content: center;
}

.forget-avatars img {
  width: 29px;
  height: 30px;
  border-radius: 50%;
  border: 0.5px solid #4e4e4e; 
  margin-left: -8px;
  object-fit: cover;
  position: relative;
  z-index: 2;
}

.forget-avatars img:first-child {
  margin-left: 0;
}

.forget-avatars span {
  font-size: 20px;
  color: #fff;
  font-family: Playfair Display;
  margin-left: 6px;
  font-weight: 600;
}

.forget-lead-text {
  position: absolute;
  bottom: 130px;
  left: 40px;
  font-size: 55px;
  font-family: poppins;
  font-weight: bold;
}






.forget-right-section {
  background: #1C1C1C;
  max-width: 410px;
  width: 110%;
  border-radius: 10px;
  margin-right: 86px;
  margin-left: -52px;
  margin-top: -40px;
}

.forget-title{
  font-size: 22px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 6px;
  font-family: poppins;
}

.forget-subtitle {
  color: #ffffff;
  text-align: center;
  font-size: 14px;
  margin-bottom: 30px;
}

.forget-form .forget-input {
  width: 414px;
  padding: 12px;
  margin-bottom: 15px;
  border: 1px solid #A7A7A7;
  background: #252525;
  color: white;
  border-radius: 6px;
  font-size: 14px;
}


.forget-submit-btn {
  padding: 12px 10px;
  font-size: 14px;
  color: white;
  font-style: poppins; 
  border: 1px solid #000;
  border-radius: 8px;
  background:linear-gradient(to bottom right, #FE8324 10% ,#FF4101 100%);
  font-weight: 600;
  width: 412px;
  height: 42px;
}




input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px #252525 inset; 
  -webkit-text-fill-color: #fff; 
  transition: background-color 5000s ease-in-out 0s;
  border: 1px solid #ccc; 
}

@media (max-width: 768px) {
  .forget-page-container {
    flex-direction: column;
    padding: 20px;
  }

  .forget-right-section {
    margin-right: 0;
    margin-top: 20px;
  }
}
